	module.exports = {"data":{"post":{"html":"<p><b>Late</b><br />\nSo, here I am performing a major update of my employer's website from the comfort of my own home. I'd normally feel more comfortable doing this after midnight rather than at 9pm, but the current sleep schedule dictates otherwise.</p>\n<p>This is the 4th web agency I've worked with, and the first that leaves a competent impression. They don't have a clue about security, though, but neither did the others.</p>\n<p><b>Middle</b><br />\nIt's snowing here, again. I thought I left Minnesota to avoid this kind of weather!</p>\n<p>North of Hamburg they're having a real blizzard, with force 11 winds (they still use the Beaufort scale here... that's around 70 mph), with lots of closed roads and schools.</p>","fields":{"title":"Overload","date":"19 Mar 2001","url":"https://papascott.de/2001-03-19-overload/"}}},"pathContext":{"slug":"/2001-03-19-overload/","prev":{"fields":{"title":"Breaking Things","date":"17 Mar 2001","slug":"/2001-03-17-breaking-things/"}},"next":{"fields":{"title":"Madman Situation","date":"20 Mar 2001","slug":"/2001-03-20-madman-situation/"}}}}

